.bg-dash-med {
    background-image: url("./../../img/bg-dash-med.png");
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: contain;
    details {
        transition: height 0.3s ease;
        summary {
            position: relative;
            list-style: none;
            cursor: pointer;
        }
        summary::-webkit-details-marker {
            display: none;
        }
        summary::after {
            position: absolute;
            transform: rotate(360deg);
            content: "";
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%230c63e4'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
            top: -5px;
            right: 16px;
            height: 33px;
            width: 33px;
        }
        .summary_recommended::after {
            top: 33px;
        }
    }
    details[open] summary::after {
        transform: rotate(180deg);
    }
    details:not([open]) {
        height: 3rem;
        @include lgm {
            min-width: 317px;
        }
    }
    details[open] {
        height: 33rem;
    }
    details[open] .moto_care_option__details__bullet_points,
    details[open] .moto_care_option__details__mounths_container,
    details[open] .moto_care_option__details__description,
    details[open] .moto_care_learn_more {
        -webkit-animation: sweep .8s ease;
    }
    @keyframes sweep {
        0%    {opacity: 0; }
        100%  {opacity: 1; }
    }
    .footer-register {
        position: inherit;
        margin-bottom: 0 !important;
    }
}

#dashboard-medical .nav-pills .nav-item.show .nav-link,
#dashboard-medical .nav-pills .nav-link {
    font-size: var(--step--1);
    font-weight: bold;
    color: #fff;
}

#dashboard-medical .nav-pills .nav-item.show .nav-link,
#dashboard-medical .nav-pills .nav-link.active {
    color: #443280;
    background: #fff;
    border-radius: 5rem;
}

.dashboard-brand {
    background-color: #443280;
}

.chart-aside {
    max-width: 25rem;
    background-color: #f8f8f8;
}

.patient-name {
    color: #443280;
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: var(--step-0);
}
.patient-name span {
    color: #606060;
}
.metric-patient {
    color: #443280;
    margin-bottom: 0;
    font-weight: bold;
    font-size: var(--step-0);
    text-align: center;
}
.reference-patient {
    font-size: var(--step--2);
}

.select-box {
    max-width: 15rem;
    width: 100%;
    flex-wrap: wrap;
}

.select-gender .form-check {
    flex: 1 1 auto;
    width: 50%;
}

.select-age .form-check {
    flex: 0 0 auto;
    width: 25%;
}

.form-check {
    box-sizing: border-box;
}

.form-check.chk-patient .form-check-input + .form-check-label {
    width: 100%;
    color: #443280;
    border: solid 1px #443280;
    text-align: center;
    font-weight: bold;
}

.form-check.chk-patient .form-check-input:checked + .form-check-label {
    color: #fff;
    background: #443280;
    text-align: center;
    font-weight: bold;
}

.select-box p {
    color: #443280;
    border: solid 1px #443280;
    width: 90px;
    text-align: center;
    font-weight: bold;
}

@media (min-width: 768px) {
    .chart-aside {
    padding-top: 2rem;
    }
    .patient-name {
    font-size: var(--step-0);
    }
    .metric-patient {
    text-align: left;
    }
    .reference-patient {
    font-size: var(--step--0);
    }
}